import React from 'react'

const OpenSourceProjects = ( ) => {
    const opsProjects = [
        {
            img:'static/img/works/currency.png',
            description:'A portfolio starter for Gatsby integrated with Contentful CMS. This website is based on the starter.'
        }      
    ]
    return (
    <div className='content-wrapper'>
        <div className="ops-container">
        {opsProjects.map((item,index)=>
            <div className="ops-card-container" key={index}>
                <div className="card ops-card">
                    <img className='card-img' src={item.img} alt=""/>
                </div>
                <h3 className='content-text-subheading'>
                    {item.description}
                </h3>
            </div>
        )}
        </div>
        <style jsx>{`
            .ops-container{
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                overflow-x: auto;
                margin-bottom: 150px;
            }
            .ops-card-container{
                display: flex;
                align-items: center;
                flex-direction: column;
                max-width:400px;
                margin:20px;

            }
            .ops-card{
                min-width:100px;
                min-height:100px;
                border-radius:14px;
                background: #fff;
                margin-bottom: 20px;
            }
        `}</style>
    </div>
    )
}

export default OpenSourceProjects